.quicknav{
    overflow:hidden;
    z-index:100;
    position: fixed;
    top: 85px;
    right: 80px;
    display: flex;
    background-color: white;
  }

  .quicknavTextHeight{
    transition: max-height 1s;
    max-height: 0px;
    overflow: hidden;
    height:0px;
  }

  .quicknavTextPadding{
    padding:0px 15px;
    transition-property: padding;
    transition-duration:.5s;
  }

  .quicknavTextHeightActive{
    max-height:100vh;
    transition: max-height .5s;
    height:auto;
  }

  .quicknavTextPaddingActive{
    padding:30px 15px;
    transition-property: padding;
    transition-duration:1s;
  }
  
  .dropDownTitle{
    box-sizing: border-box;
    height:85px;
    background-color:black;
  }
  
  .quicknavbutton{
    height:85px;
    width:70px;
    background-color:white;
  }

  .modalBackground{
    display:none;
    height:100vh;
    width:100vw;
    position:fixed;
    top:0px;
    left:0px;
    z-index: 99;
  }

  .dropDownModalBackground{
    display:block!important;
  }

  .dropdownWrapper{
    display:flex;
  }

  .dropdownWrapper div{
    flex-grow: 1;
    width: 0;
  }