body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a:link { text-decoration: none; }


a:visited { text-decoration: none; }


a:hover { text-decoration: none; }


a:active { text-decoration: none; }

.pageContainer{
  width: calc(100% - 205px);
  height: calc(100% - 100px);
  position: absolute;
  top: 85px;
  left: 205px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.title{
  line-height: 75px;
  font-size: 80px;
  color:white;
  text-align: center;
  font-family: eskapade-fraktur,serif;
  font-weight: 400;
  font-style: normal;
  padding: 0px 15px 10px 15px;
  text-align: left;
}

.black{
  background-color:black;
  color:white;
}

.headerrainbow{
  width:calc(100% - 125px);
  background-image:repeating-linear-gradient(to right, #52A9FF 0px,#52A9FF 79px, #F58514 80px, #F58514 159px, #0A70D5 160px, #0A70D5 239px, #F7A0E9 240px, #F7A0E9 319px, #31A00F 320px, #31A00F 399px, #5FD53B 400px, #5FD53B 479px, #CC3FB6 480px, #CC3FB6 559px, #E6BE38 560px, #E6BE38 639px);
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  position:fixed;
  display: flex;
  width:100vw;
  z-index:1;
  padding-bottom:5px;
}

.App-link {
  color: #61dafb;
}



.wideblock{
  width:125px;
}

.narrowblock{
  width:80px;
}

@media screen and (max-width:1080px){
  .pageContainer{
    width:100%;
    left:0px;
    top:13vw;
  }

  .wideblock.black.title{
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
  }
  .headerrainbow{
    width: 100%!important;
    height: 13vw;
    background-image:repeating-linear-gradient(to right, #CC3FB6 0vw,#CC3FB6 12.49vw,#E6BE38 12.5vw, #E6BE38 24.9vw, #52A9FF 25vw, #52A9FF 37.49vw, black 37.5vw, black 62.49vw, #F7A0E9 62.5vw, #F7A0E9 74.99vw, #31A00F 75vw, #31A00F 87.49vw,#5FD53B 87.5vw, #5FD53B 100vw);
  }
  .App-header a{
    position:absolute;
    height:13vw;
    left:50%;
    transform: translateX(-50%);
  }

  .wideblock{
    width:25vw;
    height:13vw;
  }
  .title{
    text-align: center;
    line-height: 12vw;
    font-size: 14vw;
  }
}